<template>
  <v-container fluid fill-height>
    <v-layout align-center justify-center>
      <v-flex xs12 sm8 md4 lg6>
        <v-row>
          <v-col cols="12" class="text-center">

              <h1>选择进入的应用</h1>
          </v-col>
          <v-col v-for="(item, i) in $route.params.apps" :key="i" cols="12">
            <v-card :color="item.additionalInformation.color" dark>
              <div class="d-flex flex-no-wrap justify-space-between">
                <div>
                  <v-card-title class="text-h5" v-text="item.clientName"></v-card-title>
                  <v-card-subtitle v-text="item.additionalInformation.subtitle"></v-card-subtitle>
                  <v-card-actions>
                    <v-btn  @click="redirect(i)">进入</v-btn>
                  </v-card-actions>
                </div>
                <v-avatar class="ma-3" size="125" tile>
                  <v-img :src="item.additionalInformation.src"></v-img>
                </v-avatar>
              </div>
            </v-card>
          </v-col>
        </v-row>
      </v-flex>
    </v-layout>
  </v-container>
</template>
<script>
export default {
  name: "AppList",
  data() {
    return {
      items: [
        {
          "additionalInformation": {
            color: '#1F7087',
            src: 'https://cdn.vuetifyjs.com/images/cards/foster.jpg',
            title: 'Supermodel',
            subtitle: 'Foster the People',
          },
          "clientId": "",
          "clientName": "",
          "scope": "",
          "webServerRedirectUri": ""
        },
      ],
    }
  },
  methods: {
    redirect(index) {
      let client = this.$route.params.apps[index]
      window.location.href = `http://localhost:8081/oauth/authorize?client_id=${client.clientId}&scope=all&response_type=code&redirect_uri=${client.webServerRedirectUri.split(",")[0]}`
    }
  }
}
</script>
